*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #141931;
}
a{
  text-decoration: none;
}
.color-fff{
  color: #fff;
}
.navbar-expand {
  max-width: 100%!important;
}
.list-style-none{
  list-style: none;
}
@media (min-width: 768px){

  .container, .container-md, .container-sm {
    max-width: 100%;
  }
}

